@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*, *::before, *::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: rgb(240, 240, 240);
    font-family: 'Roboto', sans-serif;
}


.main-wrapper {
    display: flex;
    justify-content: space-around;
    padding-top: 1rem;
}

.content-page {
    width: 80%;
}


.nav-links {
    padding: 1rem;
}

.nav-links ul {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.nav-links ul li {
    list-style: none;
    padding: 1rem;
}

.nav-links ul li a {
    text-decoration: none;
    color: black;
}

.nav-links ul li svg {
    margin-right: 1rem;
}

.nav-links ul li.active a{
    color: #D81E5B;
}

.nav-links ul li.active svg{
    color: #D81E5B;
}
.dialogs-page {
    display: flex;
    background-color: white;
    border-radius: 5px;
}

.dialogs-minimized {
    display: flex;
    flex-direction: column-reverse;
    justify-content: flex-end;
    border-right: 1px solid lightgrey;
    padding: .5rem;
    width: 35%;
}

.messages-container {
    display: flex;
    flex-direction: column;
}





.dialog-item {
    display: flex;
    background-color: white;
    padding: 1rem;
}

.dialog-image img {
    height: 55px;
    width: 55px;
    border-radius: 50%;
    object-fit: cover;
}

.dialog-info {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-left: 1rem;
}

.dialog-username {
    font-weight: bolder;
}

.dialog-text {
    margin-bottom: .5rem;
}
.dialog-window {
    display: flex;
    flex-direction: column;
}

.dialog-header {
    display: flex;
    align-items: center;
    padding: .5rem;
    border-bottom: 1px solid lightgrey;
}

.dialog-header p {
    font-weight: bolder;
    margin-left: 1rem;
}

.dialog-header img{
    height: 35px;
    width: 35px;
    border-radius: 50%;
    object-fit: cover;
}

.message-window {
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid lightgrey;
}






.incoming-message {
    align-self: flex-start;
    width: 40%;
    padding: .6rem;
    margin: 1rem 1rem;
    background-color: #8E8DBE;
    border-radius: 5px;
    font-family: 'Poppins', sans-serif;
}
.outgoing-message {
    align-self: flex-end;
    width: 40%;
    padding: .6rem;
    margin: 1rem 1rem;
    background-color: #59C9A5;
    border-radius: 5px;
    font-family: 'Poppins', sans-serif;
}

.outgoing-message p {
    word-wrap: break-word;
}
.send-block {
    display: flex;
    align-items: center;
    padding: .5rem;
}

.send-block textarea {
    height: 70%;
    width: 100%;
    border: none;
    outline: none;
    overflow: auto;
    resize: none;
    padding: .5rem;
    vertical-align: center;
    margin-right: .5rem;
    border-radius: 5px;
    font-family: 'Poppins', sans-serif;
    background-color: #f0f0f0;
}

.send-block button {
    height: 65%;
    border-radius: 5px;
    border: none;
    outline: none;
    cursor: pointer;
    color: white;
    background-color: #D81E5B;
    padding: 0 .8rem;
    text-align: start;
}
.find-users-page {
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 5px;
    box-shadow: 0px 5px 15px -8px rgba(0,0,0,0.3);
}

.find-users-title {
    align-self: center;
    padding: 1rem;
    font-size: 1.3rem;
}

.pages {
    margin: .5rem .5rem 1rem .5rem;
    text-align: center;
}

.page {
    padding: .5rem;
    cursor: pointer;
    font-size: 1.1rem;
}
.page-active {
    border-bottom: 1px solid black;
    padding: .5rem;
    font-size: 1.1rem;
    cursor: pointer;
}

.arrow-btn {
    cursor: pointer;
    font-size: 1.1rem;
    font-weight: bolder;
}

.users-container {
    display: flex;
    flex-direction: column-reverse;
    
}

.more-btn {
    align-self: center;
    padding: 1rem;
}

.more-btn button {
    padding: 1rem 1.5rem;
    background-color: #6BBAEC;
    color: white;
    font-weight: bolder;
    font-size: 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    outline: none;
    transition: transform 100ms linear;
}

.more-btn button:active {
    transform: scale(0.97);
}


.user {
    display: flex;
    width: 100%;
    padding: .5rem;
}

.user-header {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.follow-btn button {
    padding: .8rem;
    background-color: #6BBAEC;
    border: none;
    border-radius: 5px;
    font-weight: bolder;
    font-size: .9rem;
    cursor: pointer;
    outline: none;
    color: white;
    transition: transform 100ms linear;
}

.follow-btn button:active {
    transform: scale(0.97);
}

.follow-btn button:disabled {
    background-color: lightgrey;
}

.user-content {
    background-color: #59C9A5;
    padding: 1.5rem;
    width: 100%;
    border-radius: 10px;
}

.user-img {
    padding: 1.5rem 1.5rem .8rem 1.5rem;
}

.user-img img {
    height: 75px;
    width: 75px;
    border-radius: 50%;
    object-fit: cover;
}

.user-info-container {
    display: flex;
    justify-content: space-between;
}

.user-status {
    width: 50%;
    padding: 1rem 0;
}

.user-status p {
    font-family: 'Poppins', 'sans-serif';
}
.preloader {
    text-align: center;
}

.profile-content {
    display: flex;
    flex-wrap: wrap;
}






.profile-header {
    display: flex;
    flex-wrap: wrap;
    background-color: white;
    border-radius: 5px;
    box-shadow: 0px 5px 15px -8px rgba(0,0,0,0.3);
  }
  
  .profile-picture {
    width: 15rem;
    height: 15rem;
    padding: 1rem;
    margin-left: 1.5rem;
  }
  
  .profile-picture img{
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 50%;
  }
  
  .name-surname {
    font-size: 2rem;
    margin-bottom: .5rem;
    border-bottom: 1px solid lightgrey;
  }
  
  .profile-info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 1rem;
    margin-left: 2rem;
  }
  
  .more-info {
    display: flex;
    justify-content: space-between;
    margin-top: 1rem;
  }
  
  .more-info ul {
    list-style: none;
    margin-right: 2rem;
  }
.profile-posts {
    background-color: white;
    border-radius: 5px;
    margin-top: 1.5rem;
    margin-left: .5rem;
    padding: 0 .5rem;
    box-shadow: 0px 5px 15px -8px rgba(0,0,0,0.3);
    flex-grow: 2;
}

.posts-header {
    padding: 1rem 0;
    font-size: 1.5rem;
    margin: 0 1rem;
}

.posts {
    display: flex;
    flex-direction: column-reverse;
}


.post-item {
    display: flex;
    background-color: white;
    border-top: 1px solid lightgrey;
    padding: 1rem;
}

.post-image img {
    height: 55px;
    width: 55px;
    border-radius: 50%;
    object-fit: cover;
}

.post-info {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-left: 1rem;
}

.post-username {
    font-weight: bolder;
}

.post-text {
    margin-bottom: .5rem;
    word-wrap: break-word;
}
.post-input {
    background-color: white;
    border-radius: 5px;
    margin-top: 1.5rem;
    padding: .5rem;
    box-shadow: 0px 5px 15px -8px rgba(0,0,0,0.3);
}

.input-header {
    padding: 1rem 0;
    font-size: 1.5rem;
    margin: 0 1rem;
    text-align: center;
}

.input-block {
    position: relative;
    display: flex;
    padding-bottom: .5rem;
    justify-content: center;
}

.post-input textarea {
    max-width: 100%;
    border: none;
    outline: none;
    overflow: auto;
    resize: none;
    font-size: 1.3rem;
    margin: 0 1rem;
    padding: .5rem;
    border-radius: 5px;
    background-color: #f0f0f0;
    font-family: 'Roboto', 'sans-serif';
}

.post-btn {
    height: 10%;
    align-self: flex-end;
    padding: .7rem .7rem;
    border: none;
    border-radius: 3px;
    outline: none;
    background-color: #D81E5B;
    color: white;
    font-weight: 500;
    font-size: 1.1rem;
    font-family: 'Roboto', 'sans-serif';
    text-align: center;
    cursor: pointer;
}

.Textarea_formValidation__tZnbS textarea{
    border: 2px solid red;
}

.Textarea_formValidation__tZnbS span {
    background-color: #ff7a7a;
    border-radius: 5px;
    padding: 1rem;
    color: #ad0000;
    font-size: 1.2rem;
}
.profile-friends {
    background-color: white;
    border-radius: 5px;
    margin-top: 1.5rem;
    margin-right: .5rem;
    box-shadow: 0px 5px 15px -8px rgba(0,0,0,0.3);
    flex-grow: 1;
}

.friends-header {
    padding: 1rem 0;
    font-size: 1.5rem;
    border-bottom: 1px solid lightgrey;
    margin: 0 1rem;
}

.friends-container {
    display: flex;
    padding: .5rem;
}

.friend-item {
    padding: .5rem 1rem;
    text-align: center;
}

.friend-item img {
    height: 65px;
    width: 65px;
    border-radius: 50%;
    object-fit: cover;
}

.main-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    box-shadow: 0px 5px 15px -8px rgba(0,0,0,0.3);
}

.logo {
    padding: .5rem;
    margin-left: 1rem;
    font-size: 2rem;
    font-weight: 900;
    color: #D81E5B;
}

.header-links {
    padding: .5rem 1rem;
}

.login-link a{
    color: black;
    text-decoration: none;
}

.log-out-btn {
    border: none;
    margin-left: .5rem;
    padding: .4rem;
    border-radius: 5px;
    cursor: pointer;
}



.login-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    border-radius: 5px;
    margin-top: 1.5rem;
    margin-left: .5rem;
    padding: 2rem .5rem;
    box-shadow: 0px 5px 15px -8px rgba(0,0,0,0.3);
}

.login-form input {
    padding: .3rem .3rem;
    font-size: 1.3rem;
    margin: .5rem;
}

.login-form button {
    padding: .5rem 2rem;
    margin-top: .5rem;
    font-size: 1rem;
    border: none;
    outline: none;
    border-radius: 5px;
    background-color: #6BBAEC;
    color: white;
    font-weight: bolder;
    cursor: pointer;
}

.remember-check {
    display: flex;
    align-items: center;
}

.login-error {
    padding: .5rem;
    color: #ad0000;
    background-color: #ff7a7a;
    border-radius: 5px;
    margin-top: 1rem;
}
